.app-wrapper {
  width: 100%;
  margin: 0 auto;
}

.app-header {
  font-size: 24px;
  font-weight: 100;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  margin: 20px 0;
  padding: 0 15px;
}

@media(min-width: 768px) {
  .app-wrapper {
    width: 700px;
  }
}



