.button {
  background-color: transparent;
  border: none;
  position: absolute;
  right: 0;
  bottom: 5px;
  cursor: pointer;
  outline: none;
  color: #FFF;
  font-size: 12px;
  font-weight: 300;
  letter-spacing: 1px;
  text-transform: uppercase;
}


